import db from 'libs/db';
import React from 'react';
import { toast } from "react-toastify";
import AdminServices from 'services/api/AdminServices';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import HomePortfolioCard from "views/mobileView/components/molecules/Cards/HomePortfolioCard";
import { HiOutlineHome } from 'react-icons/hi';
import { IoEllipsisVerticalCircleOutline } from 'react-icons/io5';
import { AiOutlineInfoCircle } from 'react-icons/ai';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import FundConstituents from 'views/mobileView/components/molecules/Cards/FundConstituents';
import TransactionCard from './cards/TransactionCard';
import Modal from 'react-bootstrap/Modal';
import Note from './cards/Note';
import { BiPencil } from 'react-icons/bi';
import AdminServicesPY from 'services/api/AdminServicesPY';
import { formatDate } from 'views/mobileView/components/atoms/DateFormatter';
import SIPs from './cards/SIPs';
import GoalIcon from 'views/mobileView/components/atoms/GoalIcon';

export default function GoalScreen(props) {

  const [loading, setLoading] = React.useState(true);
  const [showMainModal, setShowMainModal] = React.useState(false)
  const [showModifySipModal, setShowModifySipModal] = React.useState(false)
  const [showEditNameModal, setShowEditNameModal] = React.useState(false)
  const [showGraphInfoModal, setShowGraphInfoModal] = React.useState(false)

  const [portfolio, setPortfolio] = React.useState({});
  const [portfolioXIRR, setPortfolioXIRR] = React.useState({});
  const [transactions, setTransactions] = React.useState([]);

  const [goalData, setGoalData] = React.useState()
	const [goalPerformanceProjection, setGoalPerformanceProjection] = React.useState({});
  const [goalSuccessRate, setGoalSuccessRate] = React.useState(0);

  const GOALID = props.location.state.GOALID;
  const MINTBOXID = props.location.state.MINTBOXID;
  const FUNDDATA = props.location.state.GOALDATA;
  const CONSTITUENTS = portfolio?.MintboxETF?.filter(f => f.MintboxId === MINTBOXID && f.GoalId === GOALID && f.MintboxETFBalance !==0).map((item) => {
      item.CurrentWeightage = item.MintboxETFBalance * 100 / FUNDDATA.MintboxBalance
      return item
  })
  React.useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true)
    try {
      db.get(db.model.user).then(async (p) => {
        await getGoalData()
        await getPortfolio(p.userData.id)
        await getPortfolioXIRR(p.userData.id)
        await getTransactions(p.userData.id)
        setLoading(false)
      })
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  };

  const getGoalData = async () => {
    var res = await AdminServices.getGoalByID(GOALID).then((res) => { return res })
    console.log(res.data[0])
    setGoalData(res.data[0])
    const goalPer = {
			"current_value": FUNDDATA.MintboxBalance,
			"target_value": FUNDDATA.GoalTargetCorpus,
			"months_to_goal": res.data[0].Tenure * 12,
			"mintbox_id": MINTBOXID,
			"sip": res.data[0].RecurringInvestment || 0,
			"growth": res.data[0].YearlyIncrementalInvestment / 100
		}

		var res2 = await AdminServicesPY.get_goal_scenarios(goalPer).then((res) => { return res })
		if (res2.type === true) {
      setGoalSuccessRate(res2.data.successRate)
			var finalData = Object.values(res2.data.data).map((obj) => {
				return Object.keys(obj).map((key) => {
					return {
						y: obj[key],
						x: new Date(key.toString().replace("T00:00:00Z", "")).getTime(),
						name: "Date:- " + formatDate(key.toString().replace("T00:00:00Z", ""))
					}
				});
			})

			var bestPerformer = 0
			var worstPerformer = 0

			var lastData = finalData.map((obj, i) => {
				return Number(obj[obj.length - 1].y)
			})

			bestPerformer = lastData.indexOf(Math.max(...lastData))
			worstPerformer = lastData.indexOf(Math.min(...lastData))

			console.log(bestPerformer, worstPerformer)

			finalData = finalData.map((obj, i) => {
				return {
					name: i === bestPerformer ? "Best Scenario" : (i === worstPerformer ? "Worst Scenario" : "Scenario:- " + (i + 1)),
					color: Number(obj[obj.length - 1].y) >= Number(FUNDDATA.GoalTargetCorpus) ? cssVar.getPropertyValue('--secondaryGreen') + "55" : cssVar.getPropertyValue('--red') + "77",
					data: obj,
					// lineWidth: i === bestPerformer || i === worstPerformer ? 3 : 1
				}
			})

			setGoalPerformanceProjection(finalData)

		} else {
			setGoalPerformanceProjection([])
		}
  }

  const getPortfolio = async (id) => {
    db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          setPortfolio(aa)
          db.set(db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        setPortfolio(p)
      }
    })
  }

  const getPortfolioXIRR = async (id) => {
    db.get(db.model.portfolioXirr).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioXIRR(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          const aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio XIRR API :", aa)
          setPortfolioXIRR(aa)
          db.set(db.model.portfolioXirr, JSON.stringify(aa))
        }
      } else {
        setPortfolioXIRR(p)
      }
    })
  }

  const getTransactions = async (id) => {
    db.get(db.model.transactions).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getTransactions(id).then(res => res);
        if (res.type === true) {
          const aa = res.data;
          console.log("Get transactions API :", aa)
          setTransactions(aa)
          db.set(db.model.transactions, JSON.stringify(aa))
        }
      } else {
        setTransactions(p)
      }
    })
  }

  const onPortfolioRefresh = async () => {
    await db.remove(db.model.portfolio);
    await db.remove(db.model.portfolioXirr);
    await db.remove(db.model.portfolioPerformance);
    await db.remove(db.model.assetAllocation);
    await db.remove(db.model.transactions)
    loadData();
  }

  const Constituents = () => {
    return <FundConstituents fundConstituents={CONSTITUENTS} type='portfolio' />
  }

  const Plan = () => {
    const yourResponse = [
      { key: 'Present cost of goal', value: CurrencyFormat(goalData?.GoalPresentValue) },
      { key: 'Years untill goal', value: goalData?.Tenure },
      { key: 'Upfront investment', value: CurrencyFormat(goalData?.LumpsumInvestment) },
      { key: 'Annual increase in SIP', value: goalData?.YearlyIncrementalInvestment+'%' },
    ]
    const contri = [
      { key: 'One-time lumpsum', value: CurrencyFormat(goalData?.LumpsumInvestment) },
      { key: 'Monthly sip', value: CurrencyFormat(goalData?.RecurringInvestment) },
    ]
    const ourAssumption = [
      { key: 'Annual returns', value: goalData?.TargetXIRR+'%' },
      { key: 'Capital gains tax', value: '10%' },
      { key: 'Inflation', value: goalData?.Inflation+'%' },
    ]
    return (
      <>
        <Note type='goal' goalDate={goalData?.CreatedOn}/>
        <div style={{padding:'4% 4% 0 4%', borderBottom:'1px solid '+cssVar.getPropertyValue('--light')}}>
          <div className='header1dark'>Your inputs</div>
          <ul>
            {
              yourResponse.map((a, i) => {
                return <li key={i} style={{ margin: "4% 0" }} className='bodyTextLight'>{a.key + ':'}
                  <span className='bodyTextDark'> {a.value}</span></li>
              })
            }
          </ul>
        </div>
        {/* <div style={{padding:'4% 4% 0 4%',borderBottom:'1px solid '+cssVar.getPropertyValue('--light')}}>
          <div className='header1dark'>Goal contributions</div>
          <ul>
            {
              contri.map((a, i) => {
                return <li key={i} style={{ margin: "4% 0" }} className='bodyTextLight'>{a.key + ':'}
                  <span className='bodyTextDark'> {a.value}</span></li>
              })
            }
          </ul>
        </div> */}
        <div style={{padding:"4% 4% 0 4%"}}>
          <div className="header1dark">Plan's assumptions</div>          
          <ul>
            {
              ourAssumption.map((a, i) => {
                return <li key={i} style={{ margin: "4% 0" }} className='bodyTextLight'>{a.key + ':'}
                  <span className='bodyTextDark'> {a.value}</span></li>
              })
            }
          </ul>
        </div>
      </>
    );
  }
  // const Contributions = () => {
  //   return (
  //     <div style={{ marginTop: '2%' }}>
  //       {
  //         portfolio?.ScheduledOrders?.filter(t => t.MintboxId === MINTBOXID).map((t, i) => {
  //           return (
  //             <>
  //               <div key={i} style={{ padding: "4%", borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
  //                 <div className='chip-sip' style={{ marginBottom: "1%" }}>
  //                   <BsDot size={24} color={t.IsDeleted === 1 ? cssVar.getPropertyValue('--red') : t.IsActive === 0 ? cssVar.getPropertyValue('--gold') : cssVar.getPropertyValue('--secondaryGreen')} style={{ marginLeft: "-10px" }} />
  //                   {t.IsDeleted === 1 ? 'Stopped' : t.IsActive === 0 ? 'Paused' : 'Active'}
  //                 </div>
  //                 <div style={{ display: "flex", alignItems: "center", gap: "6px" }}>
  //                   <div style={{ flex: 1 }}>
  //                     <div className='smallTextLight' style={{ marginBottom: "1%" }}>
  //                       {ordinal_suffix_of(t.OrderDate)} of every month
  //                     </div>
  //                     <div className='header1dark'>{CurrencyFormat(t.Amount)}</div>
  //                   </div>
  //                   <div className='header2dark secondaryGreen' onClick={() => setShowModifySipModal(true)}>Modify <AiOutlineDown size={12} /></div>
  //                 </div>
  //               </div>
  //             </>
  //           )
  //         })
  //       }
  //     </div>
  //   )
  // }

  // const Contributions = () => {
  //   return (
  //     <div style={{ marginTop: '2%' }}>
  //       <SIPs navigation={props.history} TYPE={4} FUNDCODE={MINTBOXID} FUNDDATA={FUNDDATA}/>
  //     </div>
  //   )
  // }

  const Transactions = () => {
    return (
      <div style={{ marginTop: "2%" }}>
        {
          transactions.filter(t => t.GoalId === GOALID).reverse().map((trx, i) => {
            return <TransactionCard trx={trx} i={i} type={4}/>
          })
        }
      </div>
    )
  }

  const keyTitles = [
    { title: 'Constituents', view: Constituents() },
    { title: 'Financial plan', view: Plan() },
    { title: 'SIPs', view: SIPs({navigation:props.history, TYPE:4, FUNDCODE:MINTBOXID, FUNDDATA:FUNDDATA, GOALID:GOALID }) },
    { title: 'Transactions', view: Transactions() }
  ];

  const MainModal = () => {
    const [selectedOption, setSelectedOption] = React.useState()

    const onConfirm = () => {
      switch (selectedOption) {
        case 'redeem': {
          props.history.push({
            pathname: '/portfolio/redeem-goal',
            state: { fundData: FUNDDATA , constituents :CONSTITUENTS}
          })
          setShowMainModal(false);
          break;
        }
        default: setShowMainModal(false)
      }
    }
    return (
      <Modal
        className="bottom-drawer"
        show={showMainModal}
        onHide={() => setShowMainModal(false)}
        keyboard={false}
      >
        <Modal.Body>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="redeem" id="redeem" />
            <label className="bodyTextDark radio-label" htmlFor="redeem" style={{ color: cssVar.getPropertyValue('--peach') }}>Redeem</label>
            <div className='smallTextLight' style={{marginTop:'2%'}}>Redeeming can impact your financial goal success</div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="outlineButton" onClick={() => setShowMainModal(false)}>
            Close
          </button>
          <button className="solidButton" onClick={() => onConfirm()}>
            Confirm
          </button>
        </Modal.Footer>
      </Modal>
    )
  }

  const EditNameModal = () => {
    const [newName, setNewName] = React.useState('')


    const onConfirm = async () => {
      if(newName.length>1){
        var res = await AdminServices.updateGoalName(goalData?.GoalId, newName).then((res) => { return res })
        if (res.type) {
          toast.success('Goal name updated successfully')
          await db.remove(db.model.portfolio);
          setGoalData({});
          getGoalData();
        } else {
          toast.error('Error renaming goal')
        }
        setShowEditNameModal(false);
      }
    }
    return (
      <Modal
        className="bottom-drawer"
        show={showEditNameModal}
        onHide={() => setShowEditNameModal(false)}
        keyboard={false}
      >
        <Modal.Body>
          <div className="header1dark" style={{ marginTop: "6%" }}>Renaming '{goalData?.GoalName}'</div>
          <input
            type="text"
            id="goalName"
            className='normalInput'
            placeholder='Goal name'
            value={newName}
            onChange={(n) => {
              setNewName(n.target.value);
            }}
          />
        </Modal.Body>
        <Modal.Footer>
          <button className="outlineButton" onClick={() => setShowEditNameModal(false)}>
            Close
          </button>
          <button className="solidButton" style={{ opacity : newName.length > 1?1:0.4}} onClick={() => onConfirm()}>
            Confirm
          </button>
        </Modal.Footer>
      </Modal>
    )
  }

  const ModifySipModal = () => {
    const [selectedOption, setSelectedOption] = React.useState()

    const onConfirm = () => {
      switch (selectedOption) {
        case 'editAmount': {
          setShowModifySipModal(false);
          break;
        }
        case 'editDate': {
          setShowModifySipModal(false);
          break;
        }
        case 'pauseSip': {
          setShowModifySipModal(false);
          break;
        }
        case 'stopSip': {
          setShowModifySipModal(false);
          break;
        }
        default: setShowModifySipModal(false)
      }
    }
    return (
      <Modal
        className="bottom-drawer"
        show={showModifySipModal}
        onHide={() => setShowModifySipModal(false)}
        keyboard={false}
      >
        <Modal.Body>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="editAmount" id="editAmount" />
            <label className="bodyTextDark radio-label" htmlFor="editAmount">Edit SIP amount</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="editDate" id="editDate" />
            <label className="bodyTextDark radio-label" htmlFor="editDate">Edit SIP date</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="pauseSip" id="pauseSip" />
            <label className="bodyTextDark radio-label" htmlFor="pauseSip">Pause SIP</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="stopSip" id="stopSip" />
            <label className="bodyTextDark radio-label" htmlFor="stopSip" style={{ color: cssVar.getPropertyValue('--peach') }}>Stop SIP</label>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="outlineButton" onClick={() => setShowModifySipModal(false)}>
            Close
          </button>
          <button className="solidButton" onClick={() => onConfirm()}>
            Confirm
          </button>
        </Modal.Footer>
      </Modal>
    )
  }

  const GraphInfoModal = () => {
    return (
      <Modal
        className="bottom-drawer"
        show={showGraphInfoModal}
        onHide={() => setShowGraphInfoModal(false)}
        keyboard={false}
      >
        <Modal.Body>
        <div className="bodyTextLight" style={{padding:'2%'}}>We have shown 100 potential scenarios of how your financial goal could pan out under our dynamic goal planning approach. <span className='secondaryGreen'>Green</span> lines imply goal target being achieved while <span style={{color:cssVar.getPropertyValue('--red')}}>red</span> lines imply a miss. Based on these projections, you have a <b>{goalSuccessRate}%</b> chance of achieving this goal. These scenarios are built using a technique called historical bootstrapping.</div>
        </Modal.Body>
        <Modal.Footer>
          <button className="solidButton" onClick={() => setShowGraphInfoModal(false)}>
            Close
          </button>
        </Modal.Footer>
      </Modal>
    )
  }
  if (loading) {
    return (
      <ActivityLoaderMintbox />
    );
  }

  return (
    <div className='mainContainer'>
      <MainHeader
        navigation={props.history}
        header="Goal"
      />
      <div className="dataContainer px-0">
        <div style={{ margin: "4% 4% 2%" }}>
          <div style={{ display: "flex", gap: "4%",alignItems:'center' }}>
            <GoalIcon type={FUNDDATA.GoalType} width='40px' height='40px'/>
            <div style={{ flex: 1 }}>
              <div style={{display:'flex',gap:'4%',alignItems:'center'}}>
                <div className='header1dark'>{goalData?.GoalName}</div>
                <div className='header3dark secondaryGreen' style={{ flex: 1 }} onClick={()=>setShowEditNameModal(true)}>Edit name <BiPencil size={12} /></div>
              </div>
              <div className='smallTextLight'>
                  Target <span className='smallTextDark'>{CurrencyFormat(FUNDDATA?.GoalTargetCorpus,'short',2)}</span> | Goal due in <span className='smallTextDark'>{new Date(FUNDDATA?.GoalTargetDate).getFullYear()}</span>
              </div>
            </div>
          </div>
        </div>
        <div style={{position:'relative'}} onClick={()=>setShowGraphInfoModal(true)}>
          <span style={{position:'absolute', right:'8%', top:'40px'}} className='smallTextLight'><AiOutlineInfoCircle size={20}/></span>
        </div>
        <HomePortfolioCard
          type='goal'
          navigation={props.history}
          balance={FUNDDATA?.MintboxBalance}
          previousBalance={portfolio.PrevMintboxConsolidated?.find(f => f.MintboxId === MINTBOXID && f.GoalId === GOALID)?.PrevMintboxBalance}
          xirr={Object.values(portfolioXIRR?.MintboxXIRR || {}).find(m => m.MintboxId === MINTBOXID && m.GoalId === GOALID)?.XIRR}
          onRefresh={() => { onPortfolioRefresh() }}
          growthChartData={goalPerformanceProjection}
          finalCorpus={FUNDDATA.GoalTargetCorpus}
        />
        <Tabs tabsObject={keyTitles} color={cssVar.getPropertyValue('--darkGreen')} />
      </div>
      <div className='bottom-nav'>
        <div className='bn-tab' onClick={() => { props.history.push('/dashboard') }}>
          <HiOutlineHome size={20} className='footerIcon' />
          <div className='footerText'>Home</div>
        </div>
        <div className='bn-tab' style={{ flex: 3 }}>
          <button
            className='solidButton'
            onClick={() => props.history.push({
              pathname: '/portfolio/confirm-invest',
              state: {
                fundData: FUNDDATA,
                type: 3
              }
            })}>
            Invest more
          </button>
        </div>
        <div className='bn-tab'>
          <IoEllipsisVerticalCircleOutline
            size='48'
            color={cssVar.getPropertyValue('--secondaryGreen')}
            onClick={() => setShowMainModal(true)} />
        </div>
      </div>
      <MainModal />
      <ModifySipModal />
      <EditNameModal />
      <GraphInfoModal/>
    </div>
  );
}
